<template>
  <div class="slideshows" v-if="$store.state.finditemsdata">
    <div v-if="$store.state.finditemsdata.banner">
      <van-swipe
        :autoplay="3000"
        :stop-propagation="false"
        indicator-color="white"
        @change="onChanges"
      >
        <van-swipe-item
          v-for="(item, index) in $store.state.finditemsdata.banner.list"
          :key="index"
          ><img :src="item.image" alt="" @click="gocontent"
        /></van-swipe-item>
      </van-swipe>

      <ul class="list">
        <li>
          <h1 v-if="$store.state.finditemsdata.today">
            {{ $store.state.finditemsdata.today.selection_title }}
          </h1>
          <div
            class="content"
            v-for="(item, index) in $store.state.finditemsdata.today.list"
            :key="index"
            @click="gocontenttody(item)"
          >
            <img :src="item.image" alt="" />
            <div class="cont">
              <h4>
                {{ item.cates[0].catename }}<span>/</span>{{ getmin(item) }}
              </h4>
              <h3>{{ item.title }}</h3>
            </div>
          </div>
        </li>
      </ul>
      <ul class="list">
        <li>
          <h1 v-if="$store.state.finditemsdata.hot">
            {{ $store.state.finditemsdata.hot.selection_title }}
          </h1>
          <div
            class="content"
            @click="gocontenttody($store.state.finditemsdata.hot.list[0])"
          >
            <img :src="$store.state.finditemsdata.hot.list[0].image" alt="" />
            <div class="cont">
              <h4>
                {{
                  $store.state.finditemsdata.hot.list[0].cates[0].catename
                }}/{{ getmin($store.state.finditemsdata.hot.list[0]) }}
              </h4>
              <h3>{{ $store.state.finditemsdata.hot.list[0].title }}</h3>
            </div>
          </div>
          <div class="contents">
            <div
              class="icon"
              v-for="(item, index) in $store.state.finditemsdata.hot.list"
              :key="index"
              @click="gocontenttody(item)"
            >
              <img v-if="index > 0" :src="item.image" alt="" />
              <div class="cont" v-if="index > 0">
                <h4>
                  {{ item.cates[0].catename }}<span>/</span>{{ getmin(item) }}
                </h4>
                <h3>{{ item.title }}</h3>
              </div>
            </div>
          </div>
        </li>
      </ul>
      <div class="album" v-if="$store.state.finditemsdata">
        <h1 v-if="$store.state.finditemsdata.album">
          {{ $store.state.finditemsdata.album.selection_title }}
        </h1>
        <van-swipe :autoplay="3000" indicator-color="white" @change="onChange">
          <van-swipe-item
            v-for="(item, index) in $store.state.finditemsdata.album.list"
            :key="index"
            ><img :src="item.image" alt=""
          /></van-swipe-item>
        </van-swipe>
        <div
          class="cont"
          v-for="(item, index) in $store.state.finditemsdata.album.list"
          :key="index"
        >
          <h4>
            {{ $store.state.finditemsdata.album.list[indexs].cates[0].catename
            }}<span>/</span
            >{{ getmin($store.state.finditemsdata.album.list[indexs]) }}
          </h4>
          <h3>{{ $store.state.finditemsdata.album.list[indexs].title }}</h3>
        </div>
      </div>
      <ul class="list">
        <li>
          <h1 v-if="$store.state.finditemsdata.posts">
            {{ $store.state.finditemsdata.posts.selection_title }}
          </h1>
          <div
            class="content"
            v-for="(item, index) in $store.state.finditemsdata.posts.list"
            :key="index"
            @click="gocontenttody(item)"
          >
            <img :src="item.image" alt="" />
            <div class="cont">
              <h4>
                {{ item.cates[0].catename }}<span>/</span>{{ getmin(item) }}
              </h4>
              <h3>{{ item.title }}</h3>
            </div>
          </div>
        </li>
      </ul>
    </div>
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      :error.sync="error"
      error-text="请求失败，点击重新加载"
      @load="onLoad"
    >
      <template v-if="daysitemslist[0]">
        <ul class="list" id="list">
          <van-cell v-for="(item, index) in daysitemslist" :key="index">
            <li>
              <h1>
                {{ item.selection_title }}
              </h1>
              <div
                class="content"
                v-for="(item, index) in item.list"
                :key="index"
                @click="gocontenttody(item)"
              >
                <img :src="item.image" alt="" />
                <div class="cont">
                  <h4>{{ item.cates[0].catename }}/{{ getmin(item) }}</h4>
                  <h3>{{ item.title }}</h3>
                </div>
              </div>
            </li>
          </van-cell>
        </ul>
      </template>
    </van-list>
    <div class="getheig" style="color:red">点击重新加载</div>
  </div>
</template>
<script>
// import ItemCard from "@/components/ItemCard";
export default {
  name: "FindiTems",
  // props: ["finditemsdata", "isgetdata"],
  data() {
    return {
      indexs: 0,
      indexbannner: 0,
      list: [],
      loading: false,
      error: false,
      iserror: false,
      finished: false,
      getison: true,
      daysitemslist: [],
      ispush: true,
      isdiffent: false,
      isurl: false
    };
  },
  watch: {},
  computed: {},
  methods: {
    gocontent() {
      let n = Number(
        this.$store.state.finditemsdata.banner.list[this.indexbannner]
          .extra_data.app_banner_param
      );
      if (!(String(n) == "NaN")) {
        this.$store.commit("submitIsactive", true);
        let bannerid = this.$store.state.finditemsdata.banner.list[
          this.indexbannner
        ].extra_data.app_banner_param;
        this.$router.push({ path: "finditemcontent", query: { id: bannerid } });
      }
    },

    gocontenttody(item) {
      this.$emit("nowiskeep", true);
      this.$store.commit("submitIsactive", true);
      this.$store.commit("submitLikeItem", item);
      this.$router.push({
        path: "finditemcontent",
        query: { id: item.postid }
      });
    },
    getfindheaderdata(p) {
      if (this.$store.state.isgetdata) {
        this.axios
          .get("http://api.kele8.cn/agent/" + p + "")
          .then(reponse => {
            if (this.ispush) {
              this.$store.commit(
                "submitcardpushurl",
                reponse.data.data.next_page_url_full
              );
              this.daysitemslist.push(reponse.data.data);
              this.finished = true;
              this.ispush = false;
            } else {
              this.daysitemslist.forEach(element => {
                if (element !== reponse.data.data) {
                  this.isdiffent = false;
                } else {
                  this.isdiffent = true;
                }
              });
              this.$store.state.lists.forEach(element => {
                if (element !== reponse.data.data.next_page_url_full) {
                  this.isurl = false;
                } else {
                  this.isurl = true;
                }
              });
              if (!this.isdiffent && reponse.data.data && !this.isurl) {
                this.$store.commit(
                  "submitcardpushurl",
                  reponse.data.data.next_page_url_full
                );
                this.daysitemslist.push(reponse.data.data);
                this.finished = true;
              }
            }
          })
          .catch(() => {
            this.error = true;
            this.iserror = true;
          });
      }
    },
    onScrollBottom() {
      this.$nextTick(() => {
        const container = this.$el;
        if (container)
          document.documentElement.scrollTop = container.scrollHeight;
        if (container.scrollHeight) {
          this.$emit("submitFindsctopY", container.scrollHeight);
        }
      });
    },
    onLoad() {
      this.onScrollBottom();
      if (this.iserror) {
        this.getfindheaderdata(
          this.$store.state.lists[this.$store.state.lists.length - 1]
        );
      }
      this.getfindheaderdata(
        this.$store.state.lists[this.$store.state.lists.length - 1]
      );
      // 异步更新数据
      setTimeout(() => {
        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
        if (this.list.length <= this.$store.state.lists.length) {
          this.finished = true;
        }
        this.finished = false;
      }, 500);
    },
    onChanges(i) {
      this.indexbannner = i;
    },
    onChange(i) {
      this.indexs = i;
    },
    getmin(i) {
      let time = i.duration;
      let n = Math.floor(time / 60);
      let s = Math.floor(time % 60);
      if (n < 10) {
        n = "0" + n;
      }
      if (s < 10) {
        s = "0" + s;
      }
      let m = n + "'" + s + '"';
      return m;
    }
  }
};
</script>

<style lang="less">
.slideshows {
  height: 100%;
  overflow-y: scroll;
  .van-swipe {
    height: 30vh;
    width: 100%;
    vertical-align: top;
    img {
      width: 100%;
      vertical-align: top;
    }
  }
  #list {
    .van-cell {
      padding: 0;
      &::after{
        position: static;
      }
    }
  }
  .van-list__error-text {
    line-height: 32px;
    text-align: center;
    background: #161616;
    color: white;
    height: 10vh;
  }
  .list {
    li {
      h1 {
        text-align: center;
        background: #161616;
        color: white;
        font-size: 1em;
        padding: 15px 0;
        letter-spacing: 2px;
      }
      .content {
        position: relative;
        height: 34vh;
        vertical-align: middle;
        img {
          width: 100%;
          height: 100%;
          vertical-align: middle;
        }
        .cont {
          color: white;
          position: absolute;
          bottom: 15px;
          left: 15px;
          h4 {
            font-size: 10px;
            line-height: 28px;
            font-weight: 200;
            span {
              padding: 0px 5px;
            }
          }
          h3 {
            font-size: 14px;
          }
        }
      }
      .contents {
        color: red;
        display: flex;
        flex-wrap: wrap;
        .icon {
          width: 50%;
          height: 50%;
          position: relative;
          img {
            width: 100%;
            height: 100%;
          }
          .cont {
            color: white;
            position: absolute;
            bottom: 5px;
            left: 15px;
            h4 {
              font-size: 10px;
              line-height: 20px;
              font-weight: 200;
              span {
                padding: 0px 3px;
              }
            }
            h3 {
              font-size: 13px;
              display: -webkit-box;
              -webkit-line-clamp: 1;
              -webkit-box-orient: vertical;
              overflow: hidden;
            }
          }
          &:nth-child(1) {
            display: none;
          }
        }
      }
    }
  }
  .album {
    width: 100%;
    // height: 35vh;
    position: relative;
    h1 {
      text-align: center;
      background: #161616;
      color: white;
      font-size: 1em;
      padding: 15px 0;
      letter-spacing: 2px;
    }
    .van-swipe {
      height: 100%;
      width: 100%;
      vertical-align: baseline;
      // img {
      //   width: 100%;
      //   height: 100%;
      //   vertical-align: middle;
      // }
      .van-swipe-item {
        height: 100%;
        width: 100%;
        vertical-align: baseline;
        img {
          width: 100%;
          height: 100%;
          vertical-align: middle;
        }
      }
    }
    .cont {
      position: absolute;
      bottom: 15px;
      left: 15px;
      color: white;
      h4 {
        font-size: 10px;
        line-height: 28px;
        font-weight: 100;
        letter-spacing: 1px;
        span {
          padding: 0 5px;
        }
      }
      h3 {
        font-size: 14px;
      }
    }
  }
}
</style>
