<template>
  <div class="find">
    <keep-alive>
      <FindiTems
        :finditemsdata="finditemsdata"
        :isgetdata="isgetdata"
        @submitFindsctopY="submitFindsctopY($event)"
        @nowiskeep="nowiskeep($event)"
      ></FindiTems>
    </keep-alive>
  </div>
</template>
<script>
import FindiTems from "@/components/FindiTems.vue";

export default {
  name: "find",
  data() {
    return {
      finditemsdata: null,
      isgetdata: false,
      scrollTop: 0
    };
  },
  components: {
    FindiTems
  },
  methods: {
    nowiskeep(i) {
      this.iskeep = i;
    },
    submitFindsctopY(i) {
      this.scrollTop = i - 800;
    },
    getfindheaderdata() {
      this.axios
        .get(
          "http://api.kele8.cn/agent/https://app.vmovier.com/apiv3/index/index"
        )
        .then(reponse => {
          this.finditemsdata = reponse.data.data;
          this.$store.commit("submitcardllist", reponse.data.data);
          window.localStorage.setItem(
            "finditemsdata",
            JSON.stringify({
              time: Date.now() + 60 * 60 * 1000,
              data: this.finditemsdata
            })
          );
        });
    }
  },
  created() {
    window.console.log("创建Find");
    this.getfindheaderdata();
    this.isgetdata = true;
    this.$store.commit("submitisgetdata", true);
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      let c = vm.$el.querySelector(".slideshows");
      if (vm._data.scrollTop) {
        c.scrollTop = vm._data.scrollTop;
      }
    });
  }
};
</script>
<style lang="less">
img {
  vertical-align: middle;
}
.find {
  height: 100vh;
  overflow-y: scroll;
}
</style>
